import React from 'react';

class GaleriaLista extends React.Component {
  _isMounted = false;
  constructor(props){
      super(props);

      this.state = { lista: [], erro: null };
  }
  componentDidMount(){
    this._isMounted =true;
    console.log(window.innerWidth);
    let url = "https://mrconstrucoes.herokuapp.com/imagens";
    if(window.innerWidth <= 375){
      url = "https://mrconstrucoes.herokuapp.com/imagens/minimo";
    }
    else if(window.innerWidth >= 376 && window.innerWidth <= 414){
      url = "https://mrconstrucoes.herokuapp.com/imagens/menor";
    }
    else if(window.innerWidth >= 415 && window.innerWidth <= 700){
      url = "https://mrconstrucoes.herokuapp.com/imagens/medio";
    }
    else if(window.innerWidth >= 701 && window.innerWidth <= 800){
      url = "https://mrconstrucoes.herokuapp.com/imagens/gigante";
    }
    else if(window.innerWidth >= 801 && window.innerWidth <= 1000){
      url = "https://mrconstrucoes.herokuapp.com/imagens/xgigante";
    }
    else {
      url = "https://mrconstrucoes.herokuapp.com/imagens";
    }
    fetch(url)
      .then((res) => res.json())
      .then((resposta) => { this.setState({lista: resposta}); })
      .then((error) => { this.setState({erro: error}); });
  }
  componentDidUpdate(){
    this._isMounted =true;
    console.log(window.innerWidth);
    let url = "https://mrconstrucoes.herokuapp.com/imagens";
    if(window.innerWidth <= 375){
      url = "https://mrconstrucoes.herokuapp.com/imagens/minimo";
    }
    else if(window.innerWidth >= 376 && window.innerWidth <= 414){
      url = "https://mrconstrucoes.herokuapp.com/imagens/menor";
    }
    else if(window.innerWidth >= 415 && window.innerWidth <= 700){
      url = "https://mrconstrucoes.herokuapp.com/imagens/medio";
    }
    else if(window.innerWidth >= 701 && window.innerWidth <= 800){
      url = "https://mrconstrucoes.herokuapp.com/imagens/gigante";
    }
    else if(window.innerWidth >= 801 && window.innerWidth <= 1000){
      url = "https://mrconstrucoes.herokuapp.com/imagens/xgigante";
    }
    else {
      url = "https://mrconstrucoes.herokuapp.com/imagens";
    }

    fetch(url)
      .then((res) => res.json())
      .then((resposta) => { this.setState({lista: resposta}); })
      .then((error) => { this.setState({erro: error}); });
  }
  componentWillUnmount(){
    this._isMounted = false;
  }
  render(){
    return  <div className="padding-10">
                <h2 className="text-center">Imagens dos trabalhos:</h2>

                <div className="grid container">
                  {this.state.lista.map((item, id) => {
                    return  <img src={item["imagem"]} alt={"imagem-"+id} className="image-gallery" />;
                  })}
                </div>
            </div>;
  }
}

export default GaleriaLista;
