import React from 'react';
import { BrowserRouter as Router, Switch, Route, Redirect , Link} from 'react-router-dom';
import { FaHome, FaImages, FaComments, FaConciergeBell, FaPhoneAlt } from 'react-icons/fa';
import Inicio from '../Inicio';
import Depoimentos from '../Depoimentos';
import Servicos from '../Servicos';
import Contatos from '../Contatos';
import Galeria from '../Galeria';


class Menu extends React.Component {
	constructor(props){
			super(props);

			this.handleClick = this.handleClick.bind(this);
	}
	handleClick(e){
		let menu = document.querySelectorAll("#menu > a");
		let submenu = document.querySelectorAll("#menu > a > svg");


		menu.forEach((item, i) => {
			if(e.target.id == item.id){
					item.classList.add("btn-activate");
			}
			else {
					item.classList.remove("btn-activate");
			}
		});





	}
	render(){
		return 	<Router>


			        <Redirect from="*" to="/inicio" />

					<nav className="bar  bar-mobile container raisin-black" id="menu">
						<Link to="/galeria" id="btnGaleria" name="btnGaleria" className="btn eggplant" onClick={this.handleClick}>
							<FaImages id="btnGaleria" />
							<span className="hide-on-mobile" >Galeria</span>
						</Link>

						<Link to="/depoimentos" id="btnDepoimentos"  name="btnDepoimentos" className="btn eggplant"  onClick={this.handleClick}>
							<FaComments  id="btnDepoimentos" />
							<span className="hide-on-mobile" >Depoimentos</span>
						</Link>

						<Link to="/inicio" id="btnInicio"  name="btnInicio" className="btn eggplant btn-activate"  onClick={this.handleClick}>
							<FaHome id="btnInicio" />
							<span className="hide-on-mobile" >Início</span>
						</Link>

						<Link to="/servicos" id="btnServicos" name="btnServicos"  className="btn eggplant"  onClick={this.handleClick}>
							<FaConciergeBell id="btnServicos" />
							<span className="hide-on-mobile">Serviços</span>
						</Link>

						<Link to="/contatos" id="btnContato"  name="btnContato" className="btn eggplant"  onClick={this.handleClick}>
							<FaPhoneAlt id="btnContato" />
							<span className="hide-on-mobile" >Contato</span>
						</Link>
					</nav>

						<Switch>
								<Route path="/inicio" component={Inicio} />
								<Route path="/depoimentos" component={Depoimentos} />
								<Route path="/servicos" component={Servicos} />
								<Route path="/contatos" component={Contatos} />
								<Route path="/galeria" component={Galeria} />
						</Switch>
				</Router>;
	}
}

export default Menu;
