import React from 'react';

import { FaUserAlt } from 'react-icons/fa';

class DepoimentosLista extends React.Component {
	_isMounted = false;
	constructor(props){
		super(props);

		this.state = { lista: [], erro: null, item : null };

	}
	componentDidMount(){
		this._isMounted = true;
		fetch("https://mrconstrucoes.herokuapp.com/depoimentos")
			.then((res) => res.json())
			.then((resposta) => { this.setState({ lista: resposta }); })
			.then((error) => { this.setState({ erro: error }); });
	}
	componentDidUpdate(){
		if(this._isMounted == true){
			fetch("https://mrconstrucoes.herokuapp.com/depoimentos")
				.then((res) => res.json())
				.then((resposta) => { this.setState({ lista: resposta }); })
				.then((error) => { this.setState({ erro: error }); });
			}
	}
	componentWillUnmount(){
		this._isMounted = false;
	}
	render(){
		return 	<div className="padding-10">
					<h2 className="text-center">Lista de depoimentos:</h2>

					<div className="grid container">
			{this.state.lista.map((item, id) => {
				var conteudo = null;
				if(item["imagem"] == null){
					conteudo = <FaUserAlt className="image-bordered border-circle icon-large float-left" /> ;
				}
				else{
					conteudo = <img src={item["imagem"]} alt={item["nome"]}  className="image-bordered border-circle float-left"  /> ;
				}
				return 	<div className="depoiments-container card padding-10 margin-5 column-5 light-gray" key={id}>
									<h3 className="text-center text-wrap">
										{conteudo} {item["nome"]}
									</h3>

									<p className="text-justify text-indent-15 text-limit text-quotes ">
										{item["depoimento"]}
									</p>
								</div>;
			})}
					</div>
				</div>;
	}
}


export default DepoimentosLista;
