import React from 'react';

class GaleriaForm extends React.Component {
  constructor(props){
    super(props);

    this.state = { tamanho: '', imagem: ''};

    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  handleChange(evento){
      let nome = evento.target.name;
      let valor = evento.target.value;
      this.setState({ [nome] : valor });

      console.log("State: "+JSON.stringify(this.state));
  }
  handleSubmit(evento){
    console.log("Depoimento:"+JSON.stringify(this.state));

    evento.preventDefault();

    const requestOptions = {
      method: "POST",
      headers: { "Content-Type": "application/json"},
      body: JSON.stringify(this.state)
    }

    fetch("https://mrconstrucoes.herokuapp.com/imagens", requestOptions)
      .then((response) => response.json())
      .then((data) => { alert("Salvo com sucesso!"); });

    document.querySelector("#formulario").reset();
    this.setState({tamanho: '', imagem: ''});

    let url = "https://mrconstrucoes.herokuapp.com/imagens";

    fetch("https://mrconstrucoes.herokuapp.com/imagens")
      .then((res) => res.json())
      .then((resposta) => { this.setState({lista: resposta}); })
      .then((error) => { this.setState({erro: error}); });

  }
  render(){
    return  <div>
              <form className="padding-10" id="formulario" onSubmit={this.handleSubmit}>
                <div className="card padding-10 light-gray">
                  <h3 className="text-center">Cadastrar imagem:</h3>

                  <label htmlFor="tamanho">Tamanho:</label>
                  <select className="input white border-bottom-blue-twitter-focus" value={this.state.tamanho.value} onChange={this.handleChange} name="tamanho" id="tamanho">
                    <option value="">Selecione</option>
                    <option value="minimo">minimo</option>
                    <option value="menor">menor</option>
                    <option value="medio">medio</option>
                    <option value="maior">maior</option>
                    <option value="gigante">gigante</option>
                    <option value="xgigante">xgigante</option>
                  </select>

                  <label htmlFor="imagem">Imagem:</label>
                  <input value={this.state.imagem.value} onChange={this.handleChange} className="input white border-bottom-blue-twitter-focus" id="imagem" name="imagem" placeholder="Digite a url da sua imagem" type="url" />

                  <br/>
                  <input className="btn btn-block eggplant" type="submit" />
                </div>
              </form>
            </div>;
  }
}

export default GaleriaForm;
