import React from 'react';

class DepoimentoForm extends React.Component {
  constructor(props){
    super(props);
    this.state = { nome: '', email: '', depoimento: '' };

    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  handleChange(evento){
    let nome  = evento.target.name;
    let valor = evento.target.value;
    this.setState({ [nome] : valor });

    console.log("State: "+JSON.stringify(this.state));
  }
  handleSubmit(evento){
    console.log("Depoimento: "+JSON.stringify(this.state));
    evento.preventDefault();

    const requestOptions = {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify(this.state)
    };

    fetch("https://mrconstrucoes.herokuapp.com/depoimentos", requestOptions)
      .then(response => { response.json() })
      .then(data => { alert("Salvo com sucesso!") });


    document.querySelector("#formulario").reset();
    this.setState({nome: '', email: '', depoimento: ''});

    fetch("https://mrconstrucoes.herokuapp.com/depoimentos")
			.then((res) => res.json())
			.then((resposta) => { this.setState({ lista: resposta }); })
			.then((error) => { this.setState({ erro: error }); });

  }
  render(){
    return <div>
            <form className="padding-10" id="formulario" onSubmit={this.handleSubmit}>
              <div className="card padding-10 light-gray">
                <h3 className="text-center">Deixe seu depoimento:</h3>

                <label htmlFor="nome">Nome completo:</label>
                <input value={this.state.nome.value} onChange={this.handleChange} className="input white border-bottom-blue-twitter-focus" id="nome" name="nome" placeholder="Digite seu nome completo" type="text" />

                <label htmlFor="email">Email:</label>
                <input value={this.state.email.value} onChange={this.handleChange} className="input white border-bottom-blue-twitter-focus" id="email" name="email" placeholder="Digite seu email" type="email" />

                <label htmlFor="depoimento">Depoimento:</label>
                <textarea value={this.state.depoimento.value} onChange={this.handleChange} className="input white border-bottom-blue-twitter-focus" id="depoimento" name="depoimento" row="8" cols="80" placeholder="Digite seu depoimento aqui"></textarea>

                <br/>
                <input className="btn btn-block eggplant" type="submit" />
              </div>
            </form>

          </div>;
  }
}

export default DepoimentoForm;
